.now-imagine {
   position: relative;
   width: 100%;
   margin-bottom: 80px;
   &__bg {
      min-height: 250vh;
      position: absolute;
      width: 100vw;
      overflow-x: hidden;
      top: 0;
      left: 0;
      z-index: -1;
   }
   &__bg-clouds {
      position: absolute;
      top: 0;
      left: 0;
      min-width: 100%;
      min-height: 100vh;
   }
   &__bg-stars {
      position: absolute;
      top: 0;
      left: 0;
      z-index: -1;
      min-width: 100%;
      min-height: 100vh;
   }
   &__bg-blob {
      position: absolute;
      z-index: -1;
      bottom: 0;
      left: 50%;
      width: 34%;
      width: clamp(348px, 93%, 652px);
      transform: translate(-51%, 129%);
   }
   &__wrapper {
      width: 100%;
      height: 100vh;
      display: flex;
      align-items: center;
      justify-content: center;
      background-color: rgba(222, 184, 135, 0.363);
   }
   &__body {
      text-align: center;
      max-width: 1260px;
   }
   &__title {
      margin-bottom: 16px;
   }

   &__text {
      font-family: var(--second-family);
      font-weight: 600;
      font-size: clamp(1.25rem, 3.9063vw, 2.5rem);
      line-height: 130%;
      text-align: center;
      color: var(--white);
   }
   @media (max-width: 376px) {
      &__bg-clouds {
         min-height: 94vh;
      }
   }
}

.now-imagine-clouds {
   position: absolute;
   &--img {
      img {
         width: 100%;
         height: auto;
         object-fit: cover;
      }
   }
   &__top-left {
      top: 0;
      top: 130px;
      left: 0;
      width: clamp(80px, 30vw, 766px);
      height: clamp(112px, 21vw, 406px);
   }
   &__top-right {
      top: 63px;
      right: 0;
      width: clamp(136px, 32.2396vw, 619px);
      height: clamp(94px, 19.8438vw, 381px);
   }
   &__bottom-left {
      bottom: 65px;
      left: 0;
      width: clamp(130px, 29.0104vw, 557px);
      height: clamp(116px, 20.2083vw, 388px);
   }
   &__bottom-right {
      bottom: 0;
      right: 0;
      width: clamp(169px, 30.2604vw, 581px);
      height: clamp(132px, 19.3229vw, 371px);
   }
   &__top-center {
      top: 136px;
      right: 42.6%;
      width: clamp(78px, 8.4896vw, 163px);
      height: clamp(43px, 4.7396vw, 91px);
   }
   &__bottom-center {
      bottom: 160px;
      right: 54.6%;
      width: clamp(70px, 10.5208vw, 202px);
      height: clamp(41px, 6.1458vw, 118px);
      transform: scaleX(-1);
   }
   &__top-left--big {
      position: absolute;
      bottom: 0;
      left: 0;
      width: clamp(169px, 117%, 645px);
      height: clamp(124px, 102%, 378px);
      transform: translate(clamp(-172px, -11.6211vw, -114px), 26px);
   }
   &__top-left--small {
      position: absolute;
      top: 0;
      right: 0;
      width: clamp(112px, 71%, 422px);
      height: 62%;
   }
   &__top-right--md {
      position: absolute;
      bottom: 0;
      right: 0;
      transform: translate(clamp(100px, 18vw, 353px), clamp(30px, 5vw, 103px));
      width: clamp(199px, 133%, 813px);
   }
   &__top-right--revers {
      position: absolute;
      top: 1.1979vw;
      left: 0;
      width: clamp(122px, 80%, 488px);
      transform: scaleX(-1);
   }
   &__bottom-left--big {
      position: absolute;
      top: 0;
      left: 0;
      width: clamp(202px, 125%, 694px);
      transform: translate(
         clamp(-266px, -13.8542vw, -117px),
         clamp(-21px, -1.0938vw, -5px)
      );
   }
   &__bottom-left--small {
      position: absolute;
      bottom: 0;
      right: 0;
      width: clamp(93px, 55%, 314px);
      transform: translateY(9px);
   }
   &__bottom-right--big {
      position: absolute;
      top: 0;
      right: 0;
      width: clamp(246px, 120%, 693px);
      transform: translate(clamp(127px, 13.0208vw, 250px), -11px);
   }
   &__bottom-right--small {
      position: absolute;
      top: 1.9792vw;
      left: 0;
      width: clamp(112px, 53%, 314px);
   }
   @media (max-width: 376px) {
      &__top-left {
         top: 154px;
      }
      &__top-right {
      }
      &__bottom-left {
         bottom: 13px;
      }
      &__bottom-right {
      }
      &__top-center {
         top: 68px;
         right: 48.6%;
      }
      &__bottom-center {
         bottom: 84px;
         right: 53.6%;
      }
   }
}
