.nom-imagine-moon {
   position: absolute;
   width: clamp(81px, 8.6458vw, 166px);
   top: 0;
   left: clamp(76px, 45.1042vw, 866px);
   img {
      width: 100%;
      height: 100%;
      object-fit: cover;
   }
   @media (max-width: 376px) {
      left: clamp(76px, 19.1042vw, 866px);
   }
}
.now-imagine-blob {
   &__rope {
      position: absolute;
      top: 0;
      left: 50%;
      overflow: hidden;
      transform: translate(-50%, -100%);
      width: clamp(330px, 66vw, 1267px);
      height: clamp(192px, 28vw, 480px);
   }
   &__circle {
      position: absolute;
      bottom: 0;
      left: 50%;
      transform: translate(-50%, 0);
      width: 100%;
      height: 197%;
      border-radius: 50%;
      border: 1px solid rgb(255, 255, 255);
   }
   @media (max-width: 1024px) {
      &__rope {
         height: clamp(192px, 43vw, 315px);
      }
   }
}
